import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Router, RouterStateSnapshot, ActivatedRoute } from '@angular/router';

import { PoPropar } from './po-propar';
import { Propar } from './propar';

@Injectable({
  providedIn: 'root'
})

export class PoProparService {

  constructor(
    private http: HttpClient,
  ) {

  }

  add(_po_propar: PoPropar) {
    return this.http.post<any>('Po/Propar/Add', _po_propar)
      .pipe(map(res => {
        return res;
      }));
  }

  deletePoPropar(_po_propar: PoPropar) {
    return this.http.post<any>('Po/Propar/Delete', _po_propar)
      .pipe(map(res => {
        return res;
      }));
  }

  editPoPropar(_po_propar: PoPropar) {
    return this.http.post<any>('Po/Propar/Edit', _po_propar)
      .pipe(map(res => {
        return res;
      }));
  }

  getOne(_po_propar: PoPropar): Observable<PoPropar> {
    return this.http.post<any>('Po/Propar/Get', _po_propar)
      .pipe(map(res => {
        return new PoPropar(res.PE_TICKET_ID, res.MACHINE_ID, res.PRESENCE_LOCATION_ID, res.DEVICE_ROLE);
      }));
  }
}
