import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { CommonModule } from '@angular/common';
//import { MatFileUploadModule } from 'angular-material-fileupload';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatChipsModule } from '@angular/material/chips';
import { MatSortModule } from '@angular/material/sort';
import { PoRoutingModule } from './po-routing.module';
import { MaterialModule } from '../material/material.module';
import { xFilterModule } from '../xfilter/xfilter.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { PoComponent } from './po.component';

import { AuthService } from '../auth.service';
import { AuthInterceptor } from '../auth.interceptor'

import { PoDashboardComponent } from './dashboard/po-dashboard.component';

import { PoProparComponent } from './propar/po-propar.component';
import { PoProparListComponent } from './propar/po-propar-list.component';
import { PoProparDeleteDialogComponent} from './propar/po-propar-list.component';
import { PoProparAddComponent } from './propar/po-propar-add.component';
import { PoProparEditComponent } from './propar/po-propar-edit.component';


import { Panel, PanelSubItem } from '../navigation/panel/panel';
import { PanelItem } from '../navigation/panel/panel';
import { PanelService } from '../navigation/panel/panel.service';
import { xFilterDialogComponent } from '../xfilter/xfilter.component';
import { xFilterDialogNumberComponent } from '../xfilter/xfilter.component';
import { xFilterDialogDateComponent } from '../xfilter/xfilter.component';
import { xFilterDialogTextComponent } from '../xfilter/xfilter.component';
import { PoPermissionService } from './po-permission.service';
import { ChartModule } from 'angular-highcharts';

@NgModule({
  declarations: [
    PoComponent,
    PoDashboardComponent,

    PoProparComponent,
    PoProparListComponent,
    PoProparAddComponent,
    PoProparEditComponent,
    PoProparDeleteDialogComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    PoRoutingModule,
    MaterialModule,
    FlexLayoutModule,
    //MatFileUploadModule,
    ChartModule,
    MatGridListModule,
    xFilterModule,
    MatSlideToggleModule,
    MatSortModule,
    MatChipsModule,

  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },
  ],
  bootstrap: [],
  entryComponents: [
    xFilterDialogComponent,
    xFilterDialogNumberComponent,
    xFilterDialogDateComponent,
    xFilterDialogTextComponent,
    PoProparDeleteDialogComponent
  ],
})

export class PoModule {

  constructor(
    private panelService: PanelService,
    private poPermission: PoPermissionService,
    private authService: AuthService,
  ) {

    this.authService.currentUser.subscribe(res => {
      this.panelService.messageSource.next(new Panel(
        "Production Operation", 2, [
          new PanelItem("Dashboard", "po/dashboard", "dashboard", this.poPermission.passPermission("po/dashboard")),
          new PanelItem("Propar Template", "po/propar", "list", this.poPermission.passPermission("po/propar")),

      ]));
    });

  }
}
