import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { PoPermissionService } from './po-permission.service';

@Injectable({
  providedIn: 'root'
})

export class PoPermissionGuard implements CanActivate {

  constructor(
    private router: Router,
    private poPermissionService: PoPermissionService,
  ) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    var res: boolean = this.poPermissionService.passPermission(state.url);
    if (!res) {
      this.router.navigate(['/login'], { queryParams: { returnUrl: state.url } });
    }
    return res;
  }
}
