import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../auth.guard';
import { CanDeactivateGuard } from '../can-deactivate.guard';
import { PoComponent } from './po.component';
import { PoPermissionGuard } from './po-permission.guard';

import { PoDashboardComponent } from './dashboard/po-dashboard.component';

import { PoProparComponent } from './propar/po-propar.component';
import { PoProparListComponent } from './propar/po-propar-list.component';
import { PoProparAddComponent } from './propar/po-propar-add.component';
import { PoProparEditComponent } from './propar/po-propar-edit.component';

const poRoutes: Routes = [
  {
    path: '', component: PoComponent, children: [
      { path: 'dashboard', component: PoDashboardComponent, canActivate: [PoPermissionGuard] },
      {
        path: 'propar', component: PoProparComponent, children: [
          { path: 'list', component: PoProparListComponent, canActivate: [PoPermissionGuard] },
          { path: 'add', component: PoProparAddComponent, canActivate: [PoPermissionGuard] },
          { path: 'edit/:id', component: PoProparEditComponent, canActivate: [PoPermissionGuard] },
          { path: '', redirectTo: 'list', pathMatch: "full" },
        ]
      },
      //Add here
    ]
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(poRoutes)
  ],
  exports: [
    RouterModule
  ],
  declarations: []
})

export class PoRoutingModule {

}
